import { useState, useReducer, useEffect } from 'react';
import axios from 'axios';

async function SuperFetch(
  url,
  method = 'GET',  
  body = {}
) {
  const authAxios = axios.create({
    baseURL: process.env.REACT_APP_SERVER_URL, //YOUR_API_URL HERE
    mode:'no-cors',
    headers: {
      'Content-Type': 'application/json',
    },
  });

  let res = {};
  if(method == "GET")
    res = await authAxios.get(url, body);
  else if(method == "POST")
    res = await authAxios.post(url, body);
  return res.data;
}

function dataFetchReducer(state, action) {
  switch (action.type) {
    case 'FETCH_INIT':
      return {
        ...state,
        loading: true,
        error: false,
      };
    case 'FETCH_SUCCESS':
      return {
        ...state,
        data: action.payload.slice(0, state.limit),
        total: action.payload,
        loading: false,
        error: false,
      };
    case 'FETCH_FAILURE':
      return {
        ...state,
        loading: false,
        error: true,
      };
    case 'LOAD_MORE':
      return {
        ...state,
        data: [
          ...state.data,
          ...state.total.slice(
            state.data.length,
            state.data.length + state.limit
          ),
        ],
        loading: false,
        error: false,
      };
    default:
      throw new Error();
  }
}

const useServerApi = (initialUrl, method = 'GET', body = {}, limit = 10, initialData = []) => {
  const [url, setUrl] = useState(initialUrl);

  const [state, dispatch] = useReducer(dataFetchReducer, {
    loading: false,
    error: false,
    data: initialData,
    total: initialData,
    limit: limit,
  });

  useEffect(() => {
    let didCancel = false;

    const fetchData = async () => {
      dispatch({ type: 'FETCH_INIT' });

      try {
        const result = await SuperFetch(url, method, body);        
        if (!didCancel && result.data) {
          dispatch({ type: 'FETCH_SUCCESS', payload: result.data });
        }
      } catch (error) {
        if (!didCancel) {
          dispatch({ type: 'FETCH_FAILURE' });
        }
      }
    };

    fetchData();

    return () => {
      didCancel = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [url]);
  const loadMoreData = () => {
    dispatch({ type: 'LOAD_MORE' });
  };
  const doFetch = (url) => {
    setUrl(url);
  };

  return { ...state, doFetch, loadMoreData };
};

export default useServerApi;
